.navbar {
    width: 100%;
    height: 120px;
    position: absolute;
    z-index: 10;
    background: rgba(0, 0, 0, .5);
}

.navbar .container {
    width: 100%;
    display: flex;
    /* justify-content: space-around; */
    align-items: center;
}

.navbar .logo {
    display: flex;
    align-items: center;
    color: #fff;
    margin: 1rem;
}

.logo img{
    width: 12%;
}

.navbar .logo .icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--primary-color);
}

.hamburger {
    display: none;
    padding: 1rem;
}

.navbar .nav-menu {
    height: 100%;
    align-items: center;
    display: flex;
    padding: 1rem;
}
.navbar li {
    width: max-content;
    padding: 1rem;
    font-weight: 600;
    color: #fff;
}

.navbar .icon {
    font-size: 1.4rem;
    color: #fff;
}


.navbar button {
    width: 100%;
    height: 20px;
}

@media screen and (max-width: 940px) {
    .navbar .hamburger {
        display: block;
        z-index: 15;
    }

    .logo img{
        width: 18%;
    }

    .navbar .nav-menu {
        position: absolute;
        flex-direction: column;
        width: 100%;
        height: 75vh;
        top: 0;
        left:-100%;
        align-items: center;
        justify-content: center;
        background: rgba(0, 0, 0, .8);
        transition: 0.5s ease-in;
    }

    .navbar .active {
        left: 0;
    }

    .navbar li {
        margin-right: 1rem;
        font-size: 2rem;
    }

    .navbar button {
        font-size: 1.8rem;
    }


}





